<template>
  <div>

    <div class="container p-2 mt-2 flex-non-grow">
      <div class="row mb-0">
        <div class="col-3">
          <div class="input-group input-group-sm">
            <div class="input-group-prepend">
              <span class="input-group-text" id="inputGroup-sizing-sm">日期</span>
            </div>
            <input type="date" max="9999-12-31" class="form-control" v-model="waterMeterData.date" aria-label="Small" aria-describedby="inputGroup-sizing-sm">
          </div>
        </div>
        <div class="col-3">
          <div class="input-group input-group-sm">
            <div class="input-group-prepend">
              <span class="input-group-text" id="inputGroup-sizing-sm">體重(g)</span>
            </div>
            <input type="number" step="5" min="0" form="novalidatedform" class="form-control" v-model="waterMeterData.bw" aria-label="Small" aria-describedby="inputGroup-sizing-sm">
          </div>
        </div>

        <div class="col-3">
          <div class="input-group input-group-sm">
            <div class="input-group-prepend">
              <span class="input-group-text" id="inputGroup-sizing-sm">附註</span>
            </div>
            <input type="text" class="form-control" v-model="waterMeterData.note" aria-label="Small" aria-describedby="inputGroup-sizing-sm">
          </div>
        </div>
        <div class="col-3">
          <button type="button" tabindex="-1" class="btn btn-sm" v-bind:class="[dataChanged?'btn-primary':'btn-light']" v-on:click="save">儲存</button>
          <button type="button" tabindex="-1" class="btn btn-sm" v-bind:class="[dataChanged?'btn-warning':'btn-light']" v-on:click="discardChange">捨棄</button>
          <button type="button" tabindex="-1" class="btn btn-sm" v-bind:class="[dataChanged?'btn-warning':'btn-light']" v-on:click="copyData">複製</button>
        </div>
      </div>
      <!-- <div class=" row d-flex ">
          <small class="ml-auto pr-3 ">last update: {{lastUpdate}} by {{updateBy}}</small> 
      </div> -->
    </div>
    <watermeter-table class="p-1 flex-grow flex" ref="wmtable" v-bind:wm.sync="waterMeterData.wm" v-bind:pn.sync="waterMeterData.pn" v-bind:temp.sync="temp"> </watermeter-table>
  </div>
</template>

<script>
import firebase from "firebase";
import util from "../util";
import WaterMeterTable from "./WaterMeterTable";
import $ from "jquery";
let database;
export default {
  name: "watermeterData",
  components: {
    "watermeter-table": WaterMeterTable
  },
  data: function() {
    return {
      temp: {},
      waterMeterData: {
        date: "",
        bw: 0,
        note: "",
        active: true,
        wm: {},
        pn: {}
      },
      waterMeterImage: {
        date: "",
        bw: 0,
        note: "",
        active: true,
        wm: {},
        pn: {}
      },
      dataChanged: false,
      lastUpdate: "",
      updateBy: ""
    };
  },
  methods: {
    loadWaterMeterData() {
      let vm = this;
      let waterMeterData = vm.$store.getters.currentWaterMeterData;
      if (!waterMeterData) {
        return;
      }
      vm.waterMeterImage.date = vm.waterMeterData.date = waterMeterData.date;
      vm.waterMeterImage.bw = vm.waterMeterData.bw = waterMeterData.bw;
      vm.waterMeterImage.note = vm.waterMeterData.note = waterMeterData.note;

      Object.keys(waterMeterData.wm).map(function(objectKey, index) {
        vm.waterMeterImage.wm[objectKey] = waterMeterData.wm[objectKey];
      });
      vm.waterMeterData.wm = waterMeterData.wm;

      Object.keys(waterMeterData.pn).map(function(objectKey, index) {
        vm.waterMeterImage.pn[objectKey] = waterMeterData.pn[objectKey];
      });
      vm.waterMeterData.pn = waterMeterData.pn;

      vm.waterMeterImage.active = vm.waterMeterData.active =
        waterMeterData.active;
      vm.lastUpdate = waterMeterData.lastUpdate;
      vm.updateBy = waterMeterData.updateBy;
    },
    save() {
      let vm = this;
      let currentUser = firebase.auth().currentUser.email.split("@")[0];
      database = firebase.database();

      let saveRef = database.ref(
        "/watermeter/" +
          this.$route.params.pkey +
          "/watermeterData/" +
          this.$route.params.dkey
      );
      //陣列資料會先存至temp
      let waterMeterData = vm.waterMeterData;
      waterMeterData.wm.continuousVolume = vm.temp.continuousVolume;
      waterMeterData.wm.bolusVolume = vm.temp.bolusVolume;

      saveRef.update({
        date: waterMeterData.date,
        bw: waterMeterData.bw,
        note: waterMeterData.note,
        active: waterMeterData.active,
        wm: waterMeterData.wm,
        pn: waterMeterData.pn,
        lastUpdate: util.getNow(),
        updateBy: currentUser
      });
      vm.loadWaterMeterData();
      vm.dataChanged = false;
    },
    discardChange() {
      let vm = this;
      vm.waterMeterData.date = vm.waterMeterImage.date;
      vm.waterMeterData.bw = vm.waterMeterImage.bw;
      vm.waterMeterData.note = vm.waterMeterImage.note;
      Object.keys(vm.waterMeterImage.wm).map(function(objectKey, index) {
        vm.waterMeterData.wm[objectKey] = vm.waterMeterImage.wm[objectKey];
      });
      Object.keys(vm.waterMeterImage.pn).map(function(objectKey, index) {
        vm.waterMeterData.pn[objectKey] = vm.waterMeterImage.pn[objectKey];
      });
      vm.$refs.wmtable.renderContVol();
      vm.$refs.wmtable.renderBolusVol();
      vm.waterMeterData.active = vm.waterMeterImage.active;
      vm.dataChanged = false;
    },
    copyData() {
      let vm = this;
      database = firebase.database();
      let PatientRef = database.ref(
        "/watermeter/" + vm.$route.params.pkey + "/watermeterData/"
      );
      let currentUser = firebase.auth().currentUser.email.split("@")[0];
      PatientRef.push(vm.waterMeterData);
    },
    switchActive() {
      this.waterMeterData.active = !this.waterMeterData.active;
      this.dataChanged = true;
    }
  },
  computed: {
    activeStatus() {
      let vm = this;
      return this.waterMeterData.active ? "Activate" : "Inactivate";
    }
  },
  watch: {
    $route() {
      this.loadWaterMeterData();
    },
    "$store.getters.currentWaterMeterData"() {
      if (!this.dataChanged) {
        this.loadWaterMeterData();
      }
    },
    "waterMeterData.bw"() {
      this.waterMeterData.pn.bw = this.waterMeterData.bw;
      this.waterMeterData.wm.bw = this.waterMeterData.bw;
    }
  },
  mounted() {
    var vm = this;
    vm.loadWaterMeterData();
  }
};
</script>

<style scoped>
#active-btn {
  width: 90px;
}
.flex {
  display: flex;
  flex-direction: column;
}
.flex-non-grow {
  flex: 0 0;
}
.flex-grow {
  flex: 1 0;
}
input[type="date"]::-webkit-clear-button,
input[type="date"]::-webkit-inner-spin-button,
input[type="date"]::-webkit-outer-spin-button {
  display: none;
}
input[type="date"],
input[type="number"] {
  padding-right: 0px;
}
</style>



