<template>
  <div class="outer">
    <form id="novalidatedform" novalidate />
    <div class="patient-list-container border-right">
      <div class="input-group add-patient p-3 border-bottom">
        <input type="text" class="form-control" placeholder="Search" v-model="searchPatient">
        <div class="input-group-append">
          <button class="btn btn-outline-secondary" type="button" v-on:click='addPatient'>Add</button>
        </div>
      </div>
      <div class="patient-list p-3">
        <div v-for="i in patientList" :key="i.pkey">
          <router-link v-bind:to="'/watermeter/pkey/'+i.pkey">
            {{i.name}}
          </router-link>
          <button v-on:click="deletePatient(i.pkey)">x</button>
        </div>
      </div>
    </div>
    <div class="patient">
      <div v-if="pkeyExist" class="h100">
        <router-view></router-view>
      </div>
    </div>
  </div>
</template>

<script>
import firebase from "firebase";
import util from '../util.js'
let database;
export default {
  name: "waterMeter",
  data() {
    return {
      searchPatient: ""
    };
  },
  computed: {
    patientList: function() {
      return this.$store.state.patientList;
    },
    pkeyExist: function() {
      return this.$store.getters.pkeyExist;
    }
  },
  methods: {
    addPatient: function() {
      let vm = this;
      let waterMeterRef = database.ref("/watermeter/");
      let currentUser = firebase.auth().currentUser.email.split('@')[0];
      waterMeterRef.push({
        hisid: "",
        name: "新病人",
        bedno: "",
        active: true,
        lastUpdate: util.getNow(),
        updateBy: currentUser,
        watermeter: []
      });
    },
    deletePatient: function(pkey) {
      let patientRef = database.ref("/watermeter/" + pkey + "/");
      patientRef.remove();
    },
    setRoute(route) {
      let vm = this;
      vm.$store.commit("setRoute", route);
    }
  },
  beforeRouteUpdate(to, from, next) {
    let vm = this;
    vm.setRoute(to);
    if (to.params.pkey && vm.patientList.find(x => x.pkey == to.params.pkey)) {
      next();
    } else if (!to.params.pkey) {
      next();
    } else {
      next("/watermeter");
    }
  },
  mounted() {
    let vm = this;
    vm.setRoute(vm.$route);
    database = firebase.database();
    let waterMeterRef = database.ref("/watermeter/");
    waterMeterRef.on("value", function(snapshot) {
      vm.$store.commit("setSnapshot", snapshot.val());
    });
  }
};
</script>

<style scoped>
.outer {
  width: 100%;
  height: 100%;
  display: flex;
}
.patient-list-container {
  width: 20%;
  border-color: grey;
  border-width: 2px;
  width: 200px;
  display: flex;
  flex-direction: column;
  overflow: hidden;
}
.add-patient {
  flex: 0 0 auto;
}
.patient-list {
  overflow-x: hidden;
  overflow-y: auto;
  flex: 1 1 auto;
}
.patient {
  width: 80%;
  flex: 8 0;
  display: flex;
  flex-direction: column;
  overflow: hidden;
}
.h100{
  height: 100%;
}
</style>



