<template>
  <div class="flex h100">
    <div>
      <div class="container p-2" v-show="showDetail">
        <div class="row mb-2">
          <div class="col">
            <div class="input-group input-group-sm">
              <div class="input-group-prepend">
                <span class="input-group-text" id="inputGroup-sizing-sm">床號</span>
              </div>
              <input type="text" class="form-control" v-model="patientData.bedno" aria-label="Small" aria-describedby="inputGroup-sizing-sm">
            </div>
          </div>
          <div class="col">
            <div class="input-group input-group-sm">
              <div class="input-group-prepend">
                <span class="input-group-text" id="inputGroup-sizing-sm">名稱</span>
              </div>
              <input type="text" class="form-control" v-model="patientData.name" aria-label="Small" aria-describedby="inputGroup-sizing-sm">
            </div>
          </div>
          <div class="col">
            <div class="input-group input-group-sm">
              <div class="input-group-prepend">
                <span class="input-group-text" id="inputGroup-sizing-sm">病歷號</span>
              </div>
              <input type="text" class="form-control" v-model="patientData.hisid" aria-label="Small" aria-describedby="inputGroup-sizing-sm">
            </div>
          </div>
          <div class="col-4">
            <button type="button" tabindex="-1" id="active-btn" class="btn btn-sm" v-bind:class="[patientData.active?'btn-info':'btn-light']" v-on:click="switchActive">{{activeStatus}}</button>
            <button type="button" tabindex="-1" class="btn btn-sm" v-bind:class="[dataChanged?'btn-primary':'btn-light']" v-on:click="savePatientData">儲存</button>
            <button type="button" tabindex="-1" class="btn btn-sm" v-bind:class="[dataChanged?'btn-warning':'btn-light']" v-on:click="giveUpChange">捨棄</button>
          </div>
        </div>

        <!-- <div class="row d-flex">
          <small class="ml-auto pr-3 ">last update: {{lastUpdate}} by {{updateBy}}</small> 
      </div> -->
      </div>
    </div>
    <div id="data-outer" class="border-bottom" v-show="showDetail">
      <div class="container p-2">
        <div id="data-button-container" class="flex-row">
          <button id="add-data-btn" class="btn btn-outline-secondary border btn-sm mx-1 flex-non-grow" type="button" tabindex="-1" v-on:click='addData'>Add</button>
          <div id="data-card-container">
            <router-link v-bind:to="'/watermeter/pkey/'+$route.params.pkey+ '/dkey/'+i.dkey" class="data-card card border mx-1 text-body" v-for="i in watermeterData" v-bind:key="i.dkey">
              <div class="h100">
                <div class="h50">
                  <div class="inline w50 lg" v-html="parseDate(i.date)"></div>
                  <div class="inline sm text-secondary">{{parseBW(i.bw)}}</div>
                  <button class="close" aria-label="Close" v-on:click="deleteData(i.dkey) ">
                    <span aria-hidden="true">&times;</span>
                  </button>
                </div>
                <div>
                  <span class="badge badge-primary">15%</span>
                  <span class="badge badge-warning">L</span>
                  <span class="badge badge-success">K</span>
                  <span class="badge badge-danger">Ca</span>
                </div>
              </div>

            </router-link>
          </div>
        </div>
      </div>
    </div>

    <div id="toggle" class="d-block text-center text-muted border-bottom" v-on:click="showDetail=!showDetail" v-if="dkeyExist">
      {{toggleIcon}}
    </div>
    <router-view class="flex-grow flex" v-if="dkeyExist"></router-view>
  </div>
</template>

<script>
import firebase from "firebase";
import util from "../util";
import $ from "jquery";
import $$ from "jquery-mousewheel";

let database;
export default {
  name: "watermeterPatient",
  data: function() {
    return {
      patientData: {
        name: "",
        hisid: "",
        bedno: "",
        active: ""
      },
      watermeterData: [],
      newData: "",
      lastUpdate: "",
      updateBy: "",
      patientImage: {
        name: "",
        hisid: "",
        bedno: "",
        active: ""
      },
      showDetail: true
    };
  },
  methods: {
    addData: function() {
      let vm = this;
      database = firebase.database();
      let PatientRef = database.ref(
        "/watermeter/" + vm.$route.params.pkey + "/watermeterData/"
      );
      let currentUser = firebase.auth().currentUser.email.split("@")[0];
      PatientRef.push({
        //初始WM資料
        bw: "",
        date: util.getToday(),
        note: "",
        active: true,
        updateBy: currentUser,
        lastUpdate: util.getNow(),
        wm: {
          bw: "",
          feed_type: "",
          feed_amount: "",
          feed_freq: "",
          feed_total: "",
          continuousVolume: "",
          bolusVolume: "",
          lipid_perKg: "",
          lipid_perDay: "",
          lipid_perHour: "",
          wm_summary_volume: "-",
          wm_summary_daily: "-",
          wm_summary_gir: "-",
          wm_summary_na: "-"
        },
        pn: {
          bw:"",
          pn_volume: "",
          pn_priming: "",
          pn_prescribe: "",
          aa_perKg: "",
          aa_conc: "",
          aa_prescribe: "",
          glu_perKg: "",
          glu_conc: "",
          glu_prescribe: "",
          na_perKg: "",
          na_conc: "",
          na_prescribe: "",
          k_perKg: "",
          k_conc: "",
          k_prescribe: "",
          ca_perKg: "",
          ca_conc: "",
          ca_prescribe: "",
          p_perKg: "",
          p_conc: "",
          p_prescribe: "",
          mg_perKg: "",
          mg_conc: "",
          mg_prescribe: "",
          te_perKg: "",
          te_prescribe: "",
          lyop_perKg: "",
          lyop_prescribe: "",
          pn_summary_rate: "-",
          pn_summary_gir: "-",
          pn_summary_osm: "-",
          pn_error: ""
        }
      });
    },
    deleteData: function(dkey) {
      let vm = this;
      database = firebase.database();
      let deleteRef = database.ref(
        "/watermeter/" +
          this.$route.params.pkey +
          "/watermeterData/" +
          dkey +
          "/"
      );
      deleteRef.remove();
    },
    switchActive: function() {
      this.patientData.active = !this.patientData.active;
    },
    loadPatientData: function() {
      let vm = this;
      vm.showDetail = true;
      let patient = this.$store.getters.currentPatient;
      if (!patient) {
        return;
      }
      let patientData = vm.patientData;
      let patientImage = vm.patientImage;
      patientImage.bedno = patientData.bedno = patient.bedno;
      patientImage.hisid = patientData.hisid = patient.hisid;
      patientImage.name = patientData.name = patient.name;
      patientImage.active = patientData.active = patient.active;
      vm.lastUpdate = patient.lastUpdate;
      vm.updateBy = patient.updateBy;
      vm.watermeterData = patient.watermeterData;
    },
    savePatientData: function() {
      let vm = this;
      let currentUser = firebase.auth().currentUser.email.split("@")[0];
      database = firebase.database();
      let saveRef = database.ref("/watermeter/" + this.$route.params.pkey);
      let patientData = vm.patientData;
      saveRef.update({
        bedno: patientData.bedno,
        hisid: patientData.hisid,
        name: patientData.name,
        active: patientData.active,
        lastUpdate: util.getNow(),
        updateBy: currentUser
      });
      vm.loadPatientData();
    },
    giveUpChange: function() {
      let vm = this;
      let patientData = vm.patientData;
      let patientImage = vm.patientImage;
      patientData.bedno = patientImage.bedno;
      patientData.hisid = patientImage.hisid;
      patientData.name = patientImage.name;
      patientData.active = patientImage.active;
    },
    parseDate: function(inputDate) {
      let month = inputDate.split("-")[1] || "";
      let date = inputDate.split("-")[2] || "";
      if (!month || !date) {
        return inputDate;
      }
      return month + '<span class="sm text-muted">/</span>' + date;
    },
    parseBW: function(inputBW) {
      let match = inputBW.match(/[0-9]+/);
      if (!match) {
        return "";
      } else {
        return match[0] + "g";
      }
    }
  },
  computed: {
    activeStatus: function() {
      let vm = this;
      return vm.patientData.active ? "Active" : "Inactive";
    },
    dataChanged: function() {
      let vm = this;
      let patientData = vm.patientData;
      let patientImage = vm.patientImage;
      return (
        patientImage.bedno != patientData.bedno ||
        patientImage.hisid != patientData.hisid ||
        patientImage.name != patientData.name ||
        patientImage.active != patientData.active
      );
    },
    dkeyExist: function() {
      return this.$store.getters.dkeyExist;
    },
    toggleIcon: function() {
      return this.showDetail ? "Hide" : "Show";
    }
  },
  watch: {
    $route() {
      this.loadPatientData();
    },
    "$store.getters.currentPatient"() {
      if (!this.dataChanged) {
        this.loadPatientData();
      }
    }
  },
  mounted() {
    this.loadPatientData();
    $("#data-card-container").mousewheel(function(event, delta) {
      this.scrollLeft -= delta * 30;
      event.preventDefault();
    });
  }
};
</script>

<style scoped>
#toggle {
  cursor: pointer;
}
#toggle:hover {
  background-color: whitesmoke;
}
#toggle {
  background-color: rgb(240, 240, 240);
}
#active-btn {
  width: 90px;
}
textarea {
  min-height: 55px;
}
#data-outer {
  height: 70px;
}
#data-button-container {
  height: 60px;
}
#data-card-container {
  width: 100%;
  height: 60px;
  white-space: nowrap;
  overflow-x: auto;
  overflow-y: hidden;
}
#add-data-btn {
  height: 45px;
}
.data-card {
  width: 120px;
  display: inline-block;
  height: 45px;
  text-decoration: none !important;
  padding-left: 5px;
}
.data-card:hover {
  background-color: whitesmoke;
}

.clear-both {
  display: block;
}
.flex-row {
  display: flex;
  flex-direction: row;
}
.flex {
  display: flex;
  flex-direction: column;
}
.flex-non-grow {
  flex: 0 0;
}
.flex-grow {
  flex: 1 0;
}
.h100 {
  height: 100%;
}
.h50 {
  height: 50%;
}
.w100 {
  width: 100%;
}
.w50 {
  width: 40%;
}
.inline {
  display: inline-block;
}
.sm {
  font-size: 0.8em;
}
.lg {
  font-size: 1.1em;
}
</style>



