<template>
    <div>
        <div class="container flex">
            <div class="row">
                <div class="col py-2 m-1 border text-center">
                    <div>
                        <div class="text-light bg-info">Water Meter Summary</div>
                        <table class="table table-sm summary table-info table-borderless">
                            <thead>
                                <tr>
                                    <th class="py-0" scope="col">Volume
                                        <span class="text-sm text-muted">(ml)</span>
                                    </th>
                                    <th class="py-0" scope="col">Daily
                                        <span class="text-sm text-muted">(ml/kg/D)</span>
                                    </th>
                                    <th class="py-0" scope="col">GIR
                                        <span class="text-sm text-muted">(mg/kg/min)</span>
                                    </th>
                                    <th class="py-0" scope="col">Na
                                        <span class="text-sm text-muted">(mEq/kg/D)</span>
                                    </th>
                                </tr>
                            </thead>
                            <tbody>
                                <tr>
                                    <td class="py-0 text-xl">{{wm.wm_summary_volume}}</td>
                                    <td class="py-0 text-xl">{{wm.wm_summary_daily}}</td>
                                    <td class="py-0 text-xl">{{wm.wm_summary_gir}}</td>
                                    <td class="py-0 text-xl">{{wm.wm_summary_na}}</td>
                                </tr>
                            </tbody>
                        </table>
                        <div class="text-light bg-info text-sm">Feeding</div>
                        <div class="container mt-1">
                            <div class="row">
                                <div class="col-3 px-0 pr-1">
                                    <div class="input-group input-group-sm mb-3">
                                        <div class="input-group-prepend">
                                            <span class="input-group-text py-0 bg-input-tag px-1">種類</span>
                                        </div>
                                        <input type="text" class="form-control py-0" v-model="wm.feed_type">
                                    </div>
                                </div>
                                <div class="col-3 px-0 pr-1">
                                    <div class="input-group input-group-sm mb-3">
                                        <div class="input-group-prepend">
                                            <span class="input-group-text py-0 bg-input-tag px-1">每餐</span>
                                        </div>
                                        <input type="number" min="0" form="novalidatedform" class="form-control py-0 text-center" v-model="wm.feed_amount">
                                        <div class="input-group-append">
                                            <span class="input-group-text py-0 bg-input-tag px-1">ml</span>
                                        </div>
                                    </div>
                                </div>
                                <div class="col-3 px-0">
                                    <div class="input-group input-group-sm mb-3 pr-1">
                                        <div class="input-group-prepend">
                                            <span class="input-group-text py-0 bg-input-tag px-1">每日</span>
                                        </div>
                                        <input type="number" min="1" max="8" form="novalidatedform" class="form-control py-0 text-center" v-model="wm.feed_freq">
                                        <div class="input-group-append">
                                            <span class="input-group-text py-0 bg-input-tag px-1">餐</span>
                                        </div>
                                    </div>
                                </div>
                                <div class="col-3 px-0">
                                    <div class="input-group input-group-sm mb-3">
                                        <div class="input-group-prepend">
                                            <span class="input-group-text py-0 bg-input-tag px-1">總量
                                            </span>
                                        </div>
                                        <input type="number" readonly tabindex="-1" class="form-control py-0 text-center" v-model="wm.feed_total">
                                        <div class="input-group-append">
                                            <span class="input-group-text py-0 bg-input-tag px-1">ml</span>
                                        </div>
                                    </div>
                                </div>
                            </div>
                        </div>

                        <div class="text-light bg-info text-sm">Lipid</div>
                        <div class="container mt-1">
                            <div class="row">
                                <div class="col-4 px-0">
                                    <div class="input-group input-group-sm mb-3">
                                        <div class="input-group-prepend">
                                            <span class="input-group-text py-0 bg-input-tag px-1">目標</span>
                                        </div>
                                        <input type="number" step="0.1" min="0" max="4" form="novalidatedform" class="form-control py-0 text-center" v-model="wm.lipid_perKg">
                                        <div class="input-group-append">
                                            <span class="input-group-text py-0 bg-input-tag px-1">g/kg</span>
                                        </div>
                                    </div>
                                </div>
                                <div class="col-4 px-0 px-1">
                                    <div class="input-group input-group-sm mb-3">
                                        <div class="input-group-prepend">
                                            <span class="input-group-text py-0 bg-input-tag px-1">總量</span>
                                        </div>
                                        <input type="number" step="1" min="0" form="novalidatedform" readonly tabindex="-1" class="form-control py-0 text-center" v-model="wm.lipid_perDay">
                                        <div class="input-group-append">
                                            <span class="input-group-text py-0 bg-input-tag px-1">ml</span>
                                        </div>
                                    </div>
                                </div>
                                <div class="col-4 px-0">
                                    <div class="input-group input-group-sm mb-3">
                                        <div class="input-group-prepend">
                                            <span class="input-group-text py-0 bg-input-tag px-1">in 20hr
                                            </span>
                                        </div>
                                        <input type="number" readonly tabindex="-1" class="form-control py-0 text-center" v-model="wm.lipid_perHour">
                                        <div class="input-group-append">
                                            <span class="input-group-text py-0 bg-input-tag px-1">ml/hr
                                            </span>
                                        </div>
                                    </div>
                                </div>
                            </div>
                        </div>

                        <div class="text-light bg-info text-sm pl-3">Continous Infusion
                            <div class="add-btn float-right px-2" v-on:click="addContVol">+</div>
                        </div>
                        <div class="text-muted mb-3" v-show="continousVol.length==0">no data</div>
                        <table class="table table-sm" v-show="continousVol.length>0">
                            <thead>
                                <tr>
                                    <th class="py-0 " scope="col">Item</th>
                                    <th class="py-0" scope="col">ml/hr</th>
                                    <th class="py-0" scope="col">ml/day</th>
                                    <th class="py-0" scope="col">Dex.(%)</th>
                                    <th class="py-0" scope="col">Na(mEq/L)</th>
                                    <th></th>
                                </tr>
                            </thead>
                            <tbody>
                                <tr v-for="(e,i) in continousVol" :key="i">
                                    <td><input type="text" class="form-control form-control-sm item py-0 text-center" v-model="e.item"></td>
                                    <td><input type="number" min="0" step="0.1" form="novalidatedform" class="form-control form-control-sm value py-0 text-center" v-model="e.perHour"></td>
                                    <td><input type="number" readonly tabindex="-1" min="0" step="0.1" form="novalidatedform" class="form-control form-control-sm value py-0 text-center" v-model="e.perDay"></td>
                                    <td><input type="number" min="0" step="1" form="novalidatedform" class="form-control form-control-sm value py-0 text-center" v-model="e.dex"></td>
                                    <td><input type="number" min="0" step="5" form="novalidatedform" class="form-control form-control-sm value py-0 text-center" v-model="e.na"></td>
                                    <td>
                                        <button type="button" tabindex="-1" class="close" v-on:click="removeContVol(i)">
                                            <span aria-hidden="true">&times;</span>
                                        </button>
                                    </td>
                                </tr>
                            </tbody>
                        </table>
                        <div class="text-light bg-info text-sm mt-2">Bolus Infusion
                            <div class="add-btn float-right px-2" v-on:click="addBolusVol">+</div>
                        </div>
                        <div class="text-muted mb-3" v-show="bolusVol.length==0">no data</div>
                        <table class="table table-sm" v-show="bolusVol.length>0">
                            <thead>
                                <tr>
                                    <th class="py-0" scope="col">Item</th>
                                    <th class="py-0" scope="col">ml/day</th>
                                    <th class="py-0" scope="col">Na(mEq/L)</th>
                                    <th></th>
                                </tr>
                            </thead>
                            <tbody>
                                <tr v-for="(e,i) in bolusVol" :key="i">
                                    <td><input type="text" class="form-control form-control-sm item py-0" v-model="e.item"></td>
                                    <td><input type="number" min="0" step="0.1" class="form-control form-control-sm value py-0 text-center" v-model="e.perDay"></td>
                                    <td><input type="number" min="0" step="5" class="form-control form-control-sm value py-0 text-center" v-model="e.na"></td>
                                    <td>
                                        <button type="button" tabindex="-1" class="close" @click="removeBolusVol(i)">
                                            <span aria-hidden="true">&times;</span>
                                        </button>
                                    </td>
                                </tr>
                            </tbody>
                        </table>

                    </div>
                </div>
                <div class="col py-2 m-1 border text-center">
                    <div class="text-light bg-info">TPN Summary</div>
                    <table class="table table-sm summary table-info table-borderless">
                        <thead>
                            <tr>
                                <th class="py-0" scope="col">Inf. Rate
                                    <span class="text-sm text-muted">(ml/hr)</span>
                                </th>
                                <th class="py-0" scope="col">GIR
                                    <span class="text-sm text-muted">(mg/kg/min)</span>
                                </th>
                                <th class="py-0" scope="col">Osm
                                    <span class="text-sm text-muted">(mOsm/kg)</span>
                                </th>
                            </tr>
                        </thead>
                        <tbody>
                            <tr>
                                <td class="text-xl py-0">{{pn.pn_summary_rate}}</td>
                                <td class="text-xl py-0">{{pn.pn_summary_gir}}</td>
                                <td class="text-xl py-0">{{pn.pn_summary_osm}}</td>
                            </tr>
                        </tbody>
                    </table>

                    <div class="text-light bg-info text-sm">Parental Nutrition Volume</div>

                    <div class="container">
                        <div class="row ">
                            <div class="col-4 px-0">
                                <div class="input-group input-group-sm mb-3 mt-1">
                                    <div class="input-group-prepend">
                                        <span class="input-group-text py-0 px-1">Desired</span>
                                    </div>
                                    <input type="number" min="0" step="1" class="form-control py-0 text-center" v-model="pn.pn_volume">
                                    <div class="input-group-append py-0">
                                        <span class="input-group-text py-0 px-1">ml</span>
                                    </div>
                                </div>
                            </div>
                            <div class="col-4 px-0">
                                <div class="input-group input-group-sm mb-3 mt-1 px-1">
                                    <div class="input-group-prepend">
                                        <span class="input-group-text py-0 px-1">Priming</span>
                                    </div>
                                    <input type="number" min="0" step="1" class="form-control py-0 text-center" v-model="pn.pn_priming">
                                    <div class="input-group-append py-0">
                                        <span class="input-group-text py-0 px-1">ml</span>
                                    </div>
                                </div>
                            </div>
                            <div class="col-4 px-0">
                                <div class="input-group input-group-sm mb-3 mt-1">
                                    <div class="input-group-prepend">
                                        <span class="input-group-text py-0 px-1">Prescribe</span>
                                    </div>
                                    <input type="number" min="0" step="1" readonly tabindex="-1" class="form-control py-0 text-center" v-model="pn.pn_prescribe">
                                    <div class="input-group-append py-0">
                                        <span class="input-group-text py-0 px-1">ml</span>
                                    </div>
                                </div>
                            </div>
                        </div>
                    </div>

                    <div class="text-light bg-info text-sm">Macro Nutrition</div>
                    <table class="table table-sm">
                        <thead>
                            <tr>
                                <th class="py-0" scope="col">Item</th>
                                <th class="py-0" scope="col">Target</th>
                                <th class="py-0" scope="col">Concentrate</th>
                                <th class="py-0" scope="col">Prescribe</th>
                            </tr>
                        </thead>
                        <tbody>
                            <tr>
                                <td class="text-sm">AA</td>
                                <td>
                                    <div class="input-group input-group-sm">
                                        <input type="number" min="0" step="0.1" class="form-control form-control-sm text-center py-0" v-model="pn.aa_perKg">
                                        <div class="input-group-append py-0">
                                            <span class="input-group-text py-0 text-xs">g/kg</span>
                                        </div>
                                    </div>
                                </td>
                                <td>
                                    <div class="input-group input-group-sm">
                                        <input type="number" min="0" step="0.1" readonly tabindex="-1" class="form-control form-control-sm text-center py-0" v-model="pn.aa_conc">
                                        <div class="input-group-append py-0">
                                            <span class="input-group-text py-0 text-xs">%</span>
                                        </div>
                                    </div>
                                </td>
                                <td>
                                    <div class="input-group input-group-sm">
                                        <input type="number" min="0" step="0.1" readonly tabindex="-1" class="form-control form-control-sm py-0 text-center" v-model="pn.aa_prescribe">
                                        <div class="input-group-append py-0">
                                            <span class="input-group-text py-0 text-xs">ml</span>
                                        </div>
                                    </div>
                                </td>
                            </tr>
                            <tr>
                                <td class="text-sm">Glu</td>
                                <td>
                                    <div class="input-group input-group-sm">
                                        <input type="number" min="0" step="0.1" readonly tabindex="-1" class="form-control form-control-sm py-0 text-center" v-model="pn.glu_perKg">
                                        <div class="input-group-append py-0">
                                            <span class="input-group-text py-0 text-xs">GIR</span>
                                        </div>
                                    </div>
                                </td>
                                <td>
                                    <div class="input-group input-group-sm">
                                        <input type="number" min="0" step="0.1" class="form-control form-control-sm py-0 text-center" v-model="pn.glu_conc">
                                        <div class="input-group-append py-0">
                                            <span class="input-group-text py-0 text-xs">%</span>
                                        </div>
                                    </div>
                                </td>
                                <td>
                                    <div class="input-group input-group-sm">
                                        <input type="number" min="0" step="0.1" readonly tabindex="-1" class="form-control form-control-sm py-0 text-center" v-model="pn.glu_prescribe">
                                        <div class="input-group-append py-0">
                                            <span class="input-group-text py-0 text-xs">%</span>
                                        </div>
                                    </div>

                                </td>
                            </tr>
                        </tbody>
                    </table>

                    <div class="text-light bg-info text-sm">Electrolyte</div>
                    <table class="table table-sm">
                        <thead>
                            <tr>
                                <th class="py-0" scope="col">Item</th>
                                <th class="py-0" scope="col">Target/Day</th>
                                <th class="py-0" scope="col">Concentrate</th>
                                <th class="py-0" scope="col">Prescribe</th>
                            </tr>
                        </thead>
                        <tbody>
                            <tr>
                                <td class="text-sm">Na</td>
                                <td>
                                    <div class="input-group input-group-sm">
                                        <input type="number" min="0" step="0.1" class="form-control form-control-sm py-0 text-center" v-model="pn.na_perKg">
                                        <div class="input-group-append py-0">
                                            <span class="input-group-text py-0 text-xs">mEq/kg</span>
                                        </div>
                                    </div>
                                </td>
                                <td>
                                    <div class="input-group input-group-sm">
                                        <input type="number" min="0" step="0.1" class="form-control form-control-sm py-0 text-center" readonly tabindex="-1" v-model="pn.na_conc">
                                        <div class="input-group-append py-0">
                                            <span class="input-group-text py-0 text-xs">mEq/L</span>
                                        </div>
                                    </div>
                                </td>
                                <td>
                                    <div class="input-group input-group-sm">
                                        <input type="number" min="0" step="0.1" class="form-control form-control-sm py-0 text-center" readonly tabindex="-1" v-model="pn.na_prescribe">
                                        <div class="input-group-append py-0">
                                            <span class="input-group-text py-0 text-xs">mEq</span>
                                        </div>
                                    </div>

                                </td>
                            </tr>
                            <tr>
                                <td class="text-sm">K</td>
                                <td>
                                    <div class="input-group input-group-sm">
                                        <input type="number" min="0" step="0.1" class="form-control form-control-sm py-0 text-center" v-model="pn.k_perKg">
                                        <div class="input-group-append py-0">
                                            <span class="input-group-text py-0 text-xs">mEq/kg</span>
                                        </div>
                                    </div>
                                </td>
                                <td>
                                    <div class="input-group input-group-sm">
                                        <input type="number" min="0" step="0.1" class="form-control form-control-sm py-0 text-center" readonly tabindex="-1" v-model="pn.k_conc">
                                        <div class="input-group-append py-0">
                                            <span class="input-group-text py-0 text-xs">mEq/L</span>
                                        </div>
                                    </div>
                                </td>
                                <td>
                                    <div class="input-group input-group-sm">
                                        <input type="number" min="0" step="0.1" class="form-control form-control-sm py-0 text-center" readonly tabindex="-1" v-model="pn.k_prescribe">
                                        <div class="input-group-append py-0">
                                            <span class="input-group-text py-0 text-xs">mEq</span>
                                        </div>
                                    </div>
                                </td>
                            </tr>
                            <tr>
                                <td class="text-sm">Ca</td>
                                <td>
                                    <div class="input-group input-group-sm">
                                        <input type="number" min="0" step="0.1" class="form-control form-control-sm py-0 text-center" v-model="pn.ca_perKg">
                                        <div class="input-group-append py-0">
                                            <span class="input-group-text py-0 text-xs">mEq/kg</span>
                                        </div>
                                    </div>
                                </td>
                                <td>
                                    <div class="input-group input-group-sm">
                                        <input type="number" min="0" step="0.1" class="form-control form-control-sm py-0 text-center" readonly tabindex="-1" v-model="pn.ca_conc">
                                        <div class="input-group-append py-0">
                                            <span class="input-group-text py-0 text-xs">mEq/L</span>
                                        </div>
                                    </div>
                                </td>
                                <td>
                                    <div class="input-group input-group-sm">
                                        <input type="number" min="0" step="0.1" class="form-control form-control-sm py-0 text-center" readonly tabindex="-1" v-model="pn.ca_prescribe">
                                        <div class="input-group-append py-0">
                                            <span class="input-group-text py-0 text-xs">mEq</span>
                                        </div>
                                    </div>
                                </td>
                            </tr>
                            <tr>
                                <td class="text-sm">P</td>
                                <td>
                                    <div class="input-group input-group-sm">
                                        <input type="number" min="0" step="0.1" class="form-control form-control-sm py-0 text-center" v-model="pn.p_perKg">
                                        <div class="input-group-append py-0">
                                            <span class="input-group-text py-0 text-xs">mmol/kg</span>
                                        </div>
                                    </div>
                                </td>
                                <td>
                                    <div class="input-group input-group-sm">
                                        <input type="number" min="0" step="0.1" class="form-control form-control-sm py-0 text-center" readonly tabindex="-1" v-model="pn.p_conc">
                                        <div class="input-group-append py-0">
                                            <span class="input-group-text py-0 text-xs">mmol/L</span>
                                        </div>
                                    </div>
                                </td>
                                <td>
                                    <div class="input-group input-group-sm">
                                        <input type="number" min="0" step="0.1" class="form-control form-control-sm py-0 text-center" readonly tabindex="-1" v-model="pn.p_prescribe">
                                        <div class="input-group-append py-0">
                                            <span class="input-group-text py-0 text-xs">mmol</span>
                                        </div>
                                    </div>
                                </td>
                            </tr>
                            <tr>
                                <td class="text-sm">Mg</td>
                                <td>
                                    <div class="input-group input-group-sm">
                                        <input type="number" min="0" step="0.1" class="form-control form-control-sm py-0 text-center" v-model="pn.mg_perKg">
                                        <div class="input-group-append py-0">
                                            <span class="input-group-text py-0 text-xs">mEq/kg</span>
                                        </div>
                                    </div>
                                </td>
                                <td>
                                    <div class="input-group input-group-sm">
                                        <input type="number" min="0" step="0.1" class="form-control form-control-sm py-0 text-center" readonly tabindex="-1" v-model="pn.mg_conc">
                                        <div class="input-group-append py-0">
                                            <span class="input-group-text py-0 text-xs">mEq/ml</span>
                                        </div>
                                    </div>
                                </td>
                                <td>
                                    <div class="input-group input-group-sm">
                                        <input type="number" min="0" step="0.1" class="form-control form-control-sm py-0 text-center" readonly tabindex="-1" v-model="pn.mg_prescribe">
                                        <div class="input-group-append py-0">
                                            <span class="input-group-text py-0 text-xs">mEq</span>
                                        </div>
                                    </div>
                                </td>
                            </tr>
                        </tbody>
                    </table>

                    <div class="text-light bg-info text-sm">Micro Nutrition</div>
                    <table class="table table-sm">
                        <thead>
                            <tr>
                                <th class="py-0" scope="col micro-row-name">Item</th>
                                <th class="py-0" scope="col">Target</th>
                                <th class="py-0" scope="col">Prescribe</th>
                            </tr>
                        </thead>
                        <tbody>
                            <tr>
                                <td class="text-sm micro-row-name">1/40 Trace Element</td>
                                <td>
                                    <div class="input-group input-group-sm">
                                        <input type="number" min="0" step="0.1" class="form-control form-control-sm py-0 text-center" v-model="pn.te_perKg">
                                        <div class="input-group-append py-0">
                                            <span class="input-group-text py-0 text-xs">ml/kg</span>
                                        </div>
                                    </div>
                                </td>
                                <td>
                                    <div class="input-group input-group-sm">
                                        <input type="number" min="0" step="0.1" class="form-control form-control-sm py-0 text-center" readonly tabindex="-1" v-model="pn.te_prescribe">
                                        <div class="input-group-append py-0">
                                            <span class="input-group-text py-0 text-xs">ml/kg</span>
                                        </div>
                                    </div>
                                </td>
                            </tr>
                            <tr>
                                <td class="text-sm micro-row-name">Lyo-P no K</td>

                                <td>
                                    <div class="input-group input-group-sm">
                                        <input type="number" min="0" step="0.1" class="form-control form-control-sm py-0 text-center" v-model="pn.lyop_perKg">
                                        <div class="input-group-append py-0">
                                            <span class="input-group-text py-0 text-xs">ml/kg</span>
                                        </div>
                                    </div>
                                </td>
                                <td>
                                    <div class="input-group input-group-sm">
                                        <input type="number" min="0" step="0.1" class="form-control form-control-sm py-0 text-center" readonly tabindex="-1" v-model="pn.lyop_prescribe">
                                        <div class="input-group-append py-0">
                                            <span class="input-group-text py-0 text-xs">ml/kg</span>
                                        </div>
                                    </div>
                                </td>
                            </tr>
                        </tbody>
                    </table>

                </div>
            </div>
        </div>

    </div>
</template>
<script>
Math.roundTo = function(number, digit) {
  let pow10 = Math.pow(10, digit);
  return Math.round(number * pow10) / pow10;
};

export default {
  name: "watermeter-table",
  props: ["wm", "pn", "temp"],
  data: function() {
    return {
      continousVol: [],
      bolusVol: []
    };
  },
  methods: {
    addContVol() {
      let vm = this;
      let continousVol = vm.continousVol;
      continousVol.push({
        item: "",
        perHour: "",
        perDay: "",
        dex: "",
        na: ""
      });
    },
    removeContVol(i) {
      let vm = this;
      let continousVol = vm.continousVol;
      continousVol.splice(i, 1);
    },
    renderContVol() {
      let vm = this;
      let continuousVolume = vm.wm.continuousVolume;
      vm.continousVol = [];
      if (!continuousVolume) {
        return;
      }
      vm.continousVol = JSON.parse(continuousVolume);
    },
    addBolusVol() {
      let vm = this;
      let bolusVol = vm.bolusVol;
      bolusVol.push({
        item: "",
        perDay: "",
        na: ""
      });
    },
    removeBolusVol(i) {
      let vm = this;
      let bolusVol = vm.bolusVol;
      bolusVol.splice(i, 1);
    },
    renderBolusVol() {
      let vm = this;
      let bolusVolume = vm.wm.bolusVolume;
      vm.bolusVol = [];
      if (!bolusVolume) {
        return;
      }
      vm.bolusVol = JSON.parse(bolusVolume);
    },
    waterMeterSummary() {
      let vm = this;
      let wm = vm.wm;

      let wm_summary_volume = 0;
      let glucose_mg = 0;
      let sodium_mEq = 0;

      wm.feed_total = Math.roundTo(wm.feed_amount * wm.feed_freq, 1);
      wm_summary_volume += wm.feed_total;

      vm.continousVol.forEach(e => {
        e.perDay = Math.roundTo(e.perHour * 24, 1);
        wm_summary_volume += e.perDay;
        glucose_mg += e.perDay * e.dex / 100 * 1000;
        sodium_mEq += e.perDay * e.na / 1000;
      });

      vm.bolusVol.forEach(e => {
        wm_summary_volume += Math.roundTo(e.perDay, 1);
        sodium_mEq += e.perDay * e.na / 1000;
      });

      if (wm.bw) {
        wm.lipid_perDay = Math.roundTo(wm.bw * wm.lipid_perKg / 1000 * 5, 1);
        wm_summary_volume += wm.lipid_perDay;
        wm.lipid_perHour = Math.roundTo(wm.lipid_perDay / 20, 1);
      }

      if (wm_summary_volume == 0) {
        wm.wm_summary_volume = "-";
      } else {
        wm.wm_summary_volume = Math.roundTo(wm_summary_volume, 1);
      }

      if (wm.bw && wm_summary_volume > 0) {
        wm.wm_summary_daily = Math.roundTo(wm_summary_volume / wm.bw * 1000, 1);
      } else {
        wm.wm_summary_daily = "-";
      }

      if (wm.bw && glucose_mg > 0) {
        wm.wm_summary_gir = Math.roundTo(
          glucose_mg / 24 / wm.bw * 1000 / 60,
          1
        );
      } else {
        wm.wm_summary_gir = "-";
      }

      if (wm.bw && sodium_mEq > 0) {
        wm.wm_summary_na = Math.roundTo(sodium_mEq / wm.bw * 1000, 1);
      } else {
        wm.wm_summary_na = "-";
      }
    },
    pnSummary() {
        
    }
  },
  watch: {
    $route() {
      this.renderContVol();
      this.renderBolusVol();
    },
    "wm.continuousVolume": function() {
      this.renderContVol();
    },
    continousVol: {
      handler: function() {
        let vm = this;
        vm.waterMeterSummary();
        let stringify = JSON.stringify(vm.continousVol);
        vm.temp.continuousVolume = stringify;
      },
      deep: true
    },
    "wm.bolusVolume": function() {
      this.renderBolusVol();
    },
    bolusVol: {
      handler: function() {
        let vm = this;
        vm.waterMeterSummary();
        vm.temp.bolusVolume = JSON.stringify(vm.bolusVol);
      },
      deep: true
    },
    wm: {
      handler: function() {
        this.waterMeterSummary();
      },
      deep: true
    },
    pn: {
      handler: function() {
        this.pnSummary();
      },
      deep: true
    }
  }
};
</script>

<style scoped>
.btn-toggle {
  text-align: center;
}
.col {
  overflow-y: auto;
  overflow-x: hidden;
}
.flex {
  display: flex;
  flex-direction: column;
}
.flex-non-grow {
  flex: 0 0;
}
.bg-input-tag {
  background-color: rgb(245, 245, 245);
}
.flex-grow {
  flex: 1 0;
}

.float-left {
  float: right;
  position: absolute;
}
.add-btn {
  cursor: pointer;
  background-color: rgb(18, 109, 122);
}
.add-btn:hover {
  background-color: rgb(24, 81, 90);
}
.value {
  width: 60px;
}
.micro-row-name {
  width: 180px;
}

thead {
  text-align: center;
  font-size: 0.8em;
}
tbody {
  text-align: center;
}
td {
  vertical-align: middle;
}
.summary {
  text-align: center;
}

.text-sm {
  font-size: 0.9em !important;
}
.text-xs {
  font-size: 0.8em !important;
}
.text-xl {
  font-size: 1.3em !important;
}
.text-center {
  text-align: center;
}
input[type="date"]::-webkit-clear-button,
input[type="date"]::-webkit-inner-spin-button,
input[type="date"]::-webkit-outer-spin-button {
  display: none;
}
input[type="date"],
input[type="number"] {
  padding-right: 0px;
}
</style>



